.spinner {
    margin: 100px auto;
    display: block;
    width: 36px;
    height: 36px;
  }
  
  .spinner-inner {
    width: 36px;
    height: 36px;
    animation: spin 0.7s linear infinite;
    border-radius: 300px;
    padding: 4px;
  }
  .spinner-inner svg {
    stroke-dasharray: 86;
    stroke-dashoffset: 0;
    animation: dash  0.7s ease-in-out infinite;
    animation-direction: alternate-reverse;
    stroke-width: 4px;
    transform: rotate(180deg);
  }
  
  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes dash {
    to {
      stroke-dashoffset: 80;
    }
  }
  